import React from 'react'
// 아이콘   AiOutlineLock
import {FaLongArrowAltRight} from 'react-icons/fa'
function MapMoving({robot}) {
    if (robot.current_node == robot.final_node) return null;
    return (
        <li id="arrive-item">
            <span id="arrive-robot" className="background-color-3 text-color-w">{robot.robot_id}</span>
            <span id="arrive-robot-string"> 번 로봇</span>
            {/* <FaLongArrowAltRight className="icon"/> */}
            <div id="arrow-div">
            <p id="arrow-right"></p>
            </div>
            <span id="arrive-node">{robot.final_node}</span>
        </li>
    )
}

export default MapMoving
