import axios from 'axios';
axios.defaults.baseURL = process.env.REACT_APP_SERVER_URL;    //15.165.182.103
// axios.defaults.baseURL = 'http://localhost:8080/server/src/public';    //15.165.182.103
// axios.defaults.baseURL = 'http://15.165.182.103:8080/server/src/public';    //15.165.182.103
// axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

export async function getRobotError(mapId) {    // 해당 맵의 로봇의 에러 기록들을 얻어온다

    try {
        const response = await axios.get('/robot-errors/map/' + mapId);  //
        console.log("[ErrorController] getRobotError 결과 ->");
        console.log(response);
        var status = response.status;
        if (status>= 200) {
            return [false, response.data];
        }
        return [true, "getRobotError status is not 200."];

    } catch (e) {
        console.log("[ErrorController] getRobotError error catch.\n" + e.message);
        
        return [true, e.message]
    }
}
export async function getRobotLog(logId) {    // 해당 에러와 연결된 로그의 정보를 불러옴

    try {
        const response = await axios.get('/robot-logs/' + logId);
        console.log("[ErrorController] getRobotLog 결과 ->");
        console.log(response.data);
        var status = response.status;
        var errorMessage = "";
        if (status>= 200) {
            if (response.data.length > 0) {
                return [false, response.data[0]];
            }
            else {
                errorMessage = "[ErrorController] not log data existed."
                return [true, errorMessage];
            }
        }
        else {
            return [true, "getRobotLog status is not 200."];
        }
    } catch (e) {
        console.log("[ErrorController] getRobotLog error catch.\n" + e.message);
        
        return [true, e.message]
    }
}