import React, { Component } from 'react'
// icon
import {AiOutlineWarning} from 'react-icons/ai'
import {BiLogIn} from 'react-icons/bi'

export class Authorized extends Component {
    goLogin = () => {
        document.location.href = '/login'
    }
    render() {
        return (
            <div className="content" id="authorized">
                <div className="content-title text-color-1"> <BiLogIn className="icon" />로그인</div>
                <div className="grid-container">
                    <div className="grid-item center-align" id="item-authorized">
                        <AiOutlineWarning className="icon" id="warning-icon" />
                        <p className="text-color-2" id="authorized-text">로그인이 필요합니다</p>
                        <button className="round-button text-color-w background-color-1" onClick={this.goLogin.bind(this)}>로그인하러 가기</button>
                    </div>
                </div>
            </div>
        )
    }
}

export default Authorized
