import axios from 'axios';
axios.defaults.baseURL = process.env.REACT_APP_SERVER_URL;    //15.165.182.103
// axios.defaults.baseURL = 'http://localhost:8080/server/src/public';    //15.165.182.10
// axios.defaults.baseURL = 'http://15.165.182.103:8080/server/src/public';    //15.165.182.10
// axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

export async function postControllerLogin(userId, userPassword) {    // map화면에서 목적지 node를 눌렀을 때, 픽업대의(1번) 로봇을 보내는 함수

    try {
        const response = await axios({      // 로그인 정보 서버로 보내기
            method: 'post',
            url: '/controller/login',
            data: {
                userId: userId,
                userPassword: userPassword
            }
        });
        let valid = response.data.password; 
        console.log(axios.defaults.baseURL);
        console.log("[LoginController] postControllerLogin 결과 ->");
        console.log(response);
        if (valid == true){     // 유효한 로그인 정보일 때
            return [valid, response.data];  
        }
        else if(valid == false){      // 로그인 실패
            return [valid, response.data.message];
        }
        else {
            return [false, "[LoginController] server not connected"];

        }
    } catch (e) {
        console.log("[LoginController] postControllerLogin error catch.\n" + e.message);
        
        return [false, e.message]
    }
}
