export async function getDestinationRobot(userId, destinationNode) {    // map화면에서 목적지 node를 눌렀을 때, 픽업대의(1번) 로봇을 보내는 함수

    try {
        const response = await axios.get('/controller/button/to-destination/' + userId);  // button mqtt post를 위한 정보 얻기 : robot id, map name, (final node =destinationNode)
        let error = response.data.error; 
        console.log("[MapController] getDestinationRobot to-destination 결과 ->");
        console.log(response);

        if (error) { //error일때, 즉 response.state가 201 또는 400일때
            // alert(response.data.message);
            console.log(response.data.message)
            console.log(response.data.where)
            return [error, response.data.message]
        }
        else {  // error 안났을때, 200
            let robotId = response.data.robot_id;
            let mapName = response.data.map_name;
            let postError = postButton([destinationNode, mapName, robotId]);
            return [postError, robotId];
        }
    } catch (e) {
        console.log("[MapController] getDestinationRobot error catch.\n" + e.message);
        
        return [true, e.message]
    }
}
export async function getHomeRobot(userId, robotId) {    // map화면에서 로봇버튼을 눌렀을 때, 해당 번호의 로봇을 픽업대의(1번)로 보내는 함수

    try {
        const response = await axios.get('/controller/button/to-home/' + userId + "/" + robotId);  // button mqtt post를 위한 정보 얻기 : map name
        let error = response.data.error;
        console.log("[MapController] getHomeRobot to-home 결과 ->");
        console.log(response);

        if (error) { //error일때, 즉 response.state가 201 또는 400일때
            // alert(response.data.message);
            console.log(response.data.message)
            return [error, response.data.message]
        }
        else {  // error 안났을때, 200
            let mapName = response.data.map_name;
            let postError = postButton([0, mapName, robotId]);
            return [postError, response.status];
        }
    } catch (e) {
        console.log("[MapController] getHomeRobot error catch.\n" + e.message);
        
        return [true, e.message]
    }
}

export async function getAllHomeRobot(userId) {    // map화면에서 로봇버튼을 눌렀을 때, 해당 번호의 로봇을 픽업대의(1번)로 보내는 함수

    try {
        const response = await axios.get('/controller/button/to-home-all/' + userId );  // button mqtt post를 위한 정보 얻기 : map name
        let error = response.data.error;
        console.log("[MapController] getAllHomeRobot to-home-all 결과 ->");
        console.log(response);
        if (error) { //error일때, 즉 response.state가 201 또는 400일때
            // alert(response.data.message);
            console.log(response.data.message)
            return [error, response.data.message]
        }
        else {  // error 안났을때, 200
            const robotList=response.data.robot_list;    // 해당 map의 robot list를 알아야함
            console.log("robotId lsit -> ",robotList);
            
            let mapName = response.data.map_name;
            var postError = false;
            robotList.forEach(robotId => {
                postError = postButton([0, mapName, robotId]);  // 1은 목적지. store에선 항상 복귀집이 1번이다.
                if(postError){
                    console.log("[MapController] getAllHomeRobot postError error.");
                    return [true, robotList];
                }
            });
            return [false, robotList];
        }
    } catch (e) {
        console.log("[MapController] getAllHomeRobot error catch.\n" + e.message);
        
        return [true, e.message]
    }
}
function postButton(params) {
    console.log("[MapController] postButton param ", params);
    try {
        const response = axios({
            method: 'post',
            url: '/mqtt/button',
            data: {
                table_id: params[0],
                map_name: params[1],
                robot_id: params[2]
            }
        });
        console.log("[MapController] postButton   결과 ->");
        console.log(response);
        return false;

    } catch (e) {
        console.log("[MapController] postButton button 정보를 로봇으로 보내는데 실패했습니다.\n" + e.message);
        // return [true, e.message]
        return true;
    }

}