import React, { Component } from 'react';
import CallStatus from './CallStatus';
import {getPickupRobot, getCallRobot, getIsCall} from './CallController';
import CallButton from './CallButton';
import Modal from './../Modal/Modal';

export class CallPresenter extends Component {
    constructor(props) {
        super(props)

        this.state = {
            loading: 0,
            isModalOpen:false,
            modalTitle:"제목",
            modalContent: "내용",
        };

    }

    // 로딩중에 버튼클릭 못하도록 하는 flag값을 갖는 변수 loading의 값을 변경하는 함수
    updateLoading(input) {
        this.setState(prevState => ({
            ...prevState,
            loading: input
        }))
    }

    // 모달에서 ok버튼 클릭시 
    async clickPickupModalPermit(userId){

        // 매장에 로봇이 도착한 상태. 픽업대 버튼을 누를때 시행되는 상황
        let [error, message] = await getPickupRobot(userId);
        if (!error) {
            console.log("[CallPresenter] handleButtonClick back, robot_id : ", message);
            this.setState(prevState => ({
                ...prevState,
                loading: 1, // 버튼 잠금 (로봇이 출발하지 않았을 때 다시 버튼을 누르는 것을 방지)
                isModalOpen: false
            }))
        }
        else {
            console.log("[CallPresenter] handleButtonClick back, error  : ", message);
            // alert(message);
            this.setState(prevState => ({
                ...prevState,
                loading: 0, // 버튼 오픈

                isModalOpen: false
            }))
        }
    }
    // 모달에서 ok버튼 클릭시 
    async clickKitchenModalPermit(userId){
        // 로봇이 home에 있는 상태. 호출 버튼을 누를때 시행되는 상황
        let [error2, message2] = await getCallRobot(userId);
        if (!error2) {
            console.log("[CallPresenter] handleButtonClick call, robot_id : ", error2);
            this.setState(prevState => ({
                ...prevState,
                loading: 1, // 버튼 잠금 (로봇이 호출된 뒤 출발하기 전에 다시 버튼을 누르는것을 방지)
                isModalOpen: false

            }))
        }
        else {
            console.log("[CallPresenter] handleButtonClick call, error  : ", message2);
            // alert(message2);
            this.setState(prevState => ({
                ...prevState,
                loading: 0, // 버튼 오픈
                isModalOpen: false
            }))
        }

    }
    // 주요 버튼(호출 / 픽업대 / 오는중) 눌렀을 시 동작하는 함수
        // 버튼 클릭시 모달을 띄우는 함수

    handlePickupButtonClick() {
        console.log("[CallPresenter] 픽업대로 버튼 클릭");
        // 오는 중 이거나 버튼을 누른지 3초 이내에는 handleButtonClick 함수를 끝낸다.
        if (this.state.loading) {
            console.log("[CallPresenter] loading...");
            return;
        }
        // 매장에 로봇이 도착한 상태. 픽업대 버튼을 누를때 시행되는 상황
        this.setState(prevState => ({
            ...prevState,   
            isModalOpen: "pickup",
            modalTitle: "픽업대로",
            modalContent: "해당 매장의 로봇을 픽업대로 보내겠습니까?"
        }));
    }
    handleKitchenButtonClick() {
        console.log("[CallPresenter] 호출 버튼 클릭" );
        // 오는 중 이거나 버튼을 누른지 3초 이내에는 handleButtonClick 함수를 끝낸다.
        if (this.state.loading) {
            console.log("[CallPresenter] loading...");
            return;
        }
        // 매장에 로봇이 도착한 상태. 픽업대 버튼을 누를때 시행되는 상황
        this.setState(prevState => ({
            ...prevState,
            isModalOpen: "kitchen",
            modalTitle: "로봇 호출",
            modalContent: "로봇을 주방으로 호출하시겠습니까?"
        }));

    }


    render() {
        const userId = sessionStorage.getItem('userId');
        const isModalOpen = this.state.isModalOpen;

        return (
            <div className="content" id="call">
                <h1 > 로봇 호출</h1>
                <div className="grid-container">
                    <div className="grid-item" id="item-button">
                        <h4>로봇 호출 버튼</h4>
                        <div id="calling-map-name">
                            {userId}의 매장 입니다.
                        </div>
                    
                            <CallButton userId={userId} onClickPickup={() => this.handlePickupButtonClick()} onClickKitchen={() => this.handleKitchenButtonClick()}
                                updateLoading={(input) => this.updateLoading(input)} />
                    </div>
                    <div className="item-call-status">
                        <CallStatus />

                    </div>

                </div>
                <Modal title={this.state.modalTitle} content={this.state.modalContent} open={isModalOpen} onCancel={() => this.setState(prevState => ({ isModalOpen: false }))}
                    onPermit={isModalOpen == "pickup" ? () => this.clickPickupModalPermit(userId)
                                : () => this.clickKitchenModalPermit(userId)}>
                </Modal>
            </div>
        )
    }
}

export default CallPresenter
