import React from 'react'
// 아이콘   AiOutlineLock
import {GiBattery0, GiBattery25, GiBattery50, GiBattery75, GiBattery100} from 'react-icons/gi'
function MapStatus({robot, onClick}) {
    var batteryIcon;
    if(robot.battery >= 100) {
        batteryIcon=<GiBattery100 className="icon" style={{fill:"rgb(0, 172, 0)"}}/>;
    }
    else if (robot.battery >= 75){
        batteryIcon=<GiBattery75 className="icon" style={{fill:"rgba(0, 255, 0)"}}/>;
    }
    else if (robot.battery >= 50){
        batteryIcon=<GiBattery50 className="icon" style={{fill:"rgba(255, 255, 0)"}}/>;
    }
    else if (robot.battery >= 25){
        batteryIcon=<GiBattery25 className="icon" style={{fill:"rgba(255, 127, 0)"}}/>;
    }
    else {
        batteryIcon=<GiBattery0 className="icon" style={{fill:"rgba(255, 0, 0)"}}/>;
    }
    return (
        <li className="home-button round-button background-color-4 " onClick={onClick}> 
            <span className="text-color-w"> {robot.robot_id}번 로봇</span>
            {batteryIcon}
            {/* <{robot.battery}% */}
        </li>
    )
}

export default MapStatus
