import React, { useState, useEffect } from 'react'
import axios from 'axios';
axios.defaults.baseURL = process.env.REACT_APP_SERVER_URL;    //15.165.182.103
// axios.defaults.baseURL = 'http://localhost:8080/server/src/public';    //15.165.182.103
// axios.defaults.baseURL = 'http://15.165.182.103:8080/server/src/public';     //15.165.182.103
// axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'

function CallButton(props) {
    const userId = sessionStorage.getItem('userId');
    var responseStatus = 0;
    const [buttonStatus, setButtonStatus] = useState(0);
    const [calledRobot, setCalledRobot] = useState(0);

    function getIsCall() {
        var interval = setInterval(() => {
            axios.get('/controller/button/is-call/' + userId)   // 현재 버튼 상태를 반복적으로 불러옴
                .then((response) => {                    
                    responseStatus = response.status    // 201 이동중 202 주방 200 픽업대에 위치
                    switch (responseStatus) {
                        case 201:
                            console.log("[CallButton] 현재 매장의 로봇 이동중 : " + responseStatus);
                            if (buttonStatus != 1) {
                                // console.log(buttonStatus+"업데이트!");
                                props.updateLoading(0); //로봇의 움직임이 명령에 반응하여 다른 is call이 반응 했을 때 버튼 클릭 가능
                                setButtonStatus(1);
                            }
                            setCalledRobot(response.data.robot_id); // 해당 주방으로 이동중인 로봇 id 
                            setButtonStatus(1);
                            break;
                        case 202:
                            console.log("[CallButton] 현재 매장의 로봇 도착 : " + responseStatus);
                            if (buttonStatus != 2) {
                                // console.log(buttonStatus+"업데이트! ");
                                props.updateLoading(0); //로봇의 움직임이 명령에 반응하여 다른 is call이 반응 했을 때 버튼 클릭 가능
                                setButtonStatus(2);
                            }
                            setCalledRobot(response.data.robot_id); // 현재 주방에 도착한 로봇id
                            setButtonStatus(2);
                            break;
                        case 200:
                            console.log("[CallButton] 현재 매장의 로봇 호출안함 : " + responseStatus);
                            if (buttonStatus != 0) {
                                // console.log(buttonStatus+"업데이트!");
                                props.updateLoading(0); //로봇의 움직임이 명령에 반응하여 다른 is call이 반응 했을 때 버튼 클릭 가능
                                setButtonStatus(0);
                            }
                                setButtonStatus(0);
                            break;
                        default:
                            break;
                    }
                    // return 1;
                })
                .catch((error) => {
                    console.log("[CallButton] fail  get is-call button. error ->");
                    console.log(error);
                    clearInterval(interval);

                    return 0;
                })
            // if (buttonStatus != 1 && count == 3) {       // 로봇이 오는중이 아니고 3번의 로드 후
            //     console.log("[CallButton] 버튼 제한 풀림");
            //     props.updateLoading(0); //버튼 잠금 푼다. 3초 로딩 후 버튼 허가
            // }

        }, 1000);
        return interval;

    }

    useEffect(() => {
        const interval = getIsCall();   
        return () => clearInterval(interval);   // 해당 function이 종료될 때 반복도 종료됨.  
    }, [buttonStatus])

    return (
        <div >
            <div id="called-robot">
                {buttonStatus != 0 ?
                    <div><span>{calledRobot}</span> 번 로봇 배정 </div>
                    :
                    <div> </div>    // '호출'버튼일 때에는 배정된 로봇이 없다.
                }

            </div>
            {
                buttonStatus == 0 ? <button id="call-button"  onClick={props.onClickKitchen}>호출</button>
                : buttonStatus == 2 ? <button id="call-button"  onClick={props.onClickPickup}>픽업대</button>
                : <button id="call-button">오는중</button>
            }
        </div>
    )
}

export default CallButton
