import './App.css';
import React, { useEffect, useState } from 'react';
import Login from './component/User/Login';
import User from './component/User/User';
import Authorized from './component/User/Authorized';
import Map from './component/Map/MapPresenter';
import Call from './component/Call/CallPresenter';
import Setting from './component/Setting/SettingPresenter';
import History from './component/History/HistoryPresenter';
import Error from './component/Error/ErrorPresenter';
import Pickup from './component/Pickup/PickupPresenter';
import SideNav from './component/SideNav/SideNavPresenter';
import {  BrowserRouter as Router,  Route,  Redirect,  Switch} from 'react-router-dom';
import { createTheme }  from '@material-ui/core/styles'
import {createMuiTheme, ThemeProvider } from '@material-ui/core/styles';


function App() {

  //로그인 상태 관리
  const [isAuthorized, setIsAuthorized] = useState(false) //로그인 상태. 기본 false

  useEffect(() => {
    const user = sessionStorage.getItem('mapId')
    if (user == null) {
      // sessionStorage 에 user_id 라는 key 값으로 저장된 값이 없다면
      // alert('isLogin ?? no:: ' + isAuthorized)
    } else {
      // sessionStorage 에 user_id 라는 key 값으로 저장된 값이 있다면
      // 로그인 상태 변경
      setIsAuthorized(true)
      // alert('isLogin ?? yes:: map ' + user)
    }
  })
const theme = createTheme({ // 로그인만 사용
  palette: {
    primary: {
      main: '#688ABD', //로그인 버튼, 테두리 색상
      light: "#ff0000",
      dark: "#0000ff"
    },
    secondary: {
      main: '#446AA3', // item의 title 색상
    },
    error: {
      main: '#6B9ED9', // 모달 색상
      // light: "#6B9ED9",
      // dark: "#6B9ED9"
    },
    sidenav: {    // 사이드바 색상
      main: '#446AA3',
      background: '#124D84',
      text: '#C0BEFF',
    },
    text: {
      primary: '#384C68',
      secondary: '#688ABD', // 로그인 아이디 인풋 전 색상. 왜인지 모름
      disabled: '#4caf50',
      hint: '#ffc107',
      myTextColor : '#384C68'
    }
  },
  overrides: {
    MuiButton: {
      label: {
        color: "#f1f1f1",
      },
    },
  },
})

  return (
    <ThemeProvider theme={theme}>

    <div className="web-container">
      <Router>
        <div className="web-side-nav">
          <SideNav isAuthorized={isAuthorized}/>
        </div>
        <div className="web-content">
          <Switch>
            <Route path="/" exact>

              {!isAuthorized ? <Login /> : <Setting />}
            </Route>
            <Route path="/map" exact>
              {!isAuthorized ? < Authorized /> : <Map />}
            </Route>
            <Route path="/call" exact>
              {!isAuthorized ? < Authorized /> : <Call />}
            </Route>
            <Route path="/pickup" exact>
              {!isAuthorized ? < Authorized /> : <Pickup />}
            </Route>
            <Route path="/history" exact>
              {!isAuthorized ? < Authorized /> : <History />}
            </Route>
            <Route path="/error" exact>
              {!isAuthorized ? < Authorized /> : <Error />}
            </Route>
            <Route path="/setting" exact>
              <Setting />
            </Route>
            <Route path="/login" exact>
              < Login />
            </Route>
            <Route path="/my-page" exact>
              < User />
            </Route>

            <Redirect to="/" />
            {/* 주어지지 않은 url은 home으로 자동 이동 */}
          </Switch>
        </div>
      </Router>
    </div>
    </ThemeProvider>

  );
}

export default App;