import React, { useEffect, useState } from 'react';
import {NavLink} from "react-router-dom";
// import './SideNav.css';  MdRunningWithErrors
import './../../assets/style.css';
import {AiOutlineLeft,AiOutlineRight, AiOutlineRobot, AiOutlineHistory, AiOutlineSetting, AiOutlineUser} from 'react-icons/ai'
import {FaEquals,FaMapMarkerAlt, FaParking} from 'react-icons/fa'
import {MdErrorOutline} from 'react-icons/md'

function SideNavPresenter({isAuthorized}) {
    const menus = [
        { name: "실시간 로봇위치", path: "/map", iconClassName: "bi bi-speedometer2"},
        { name: "로봇 호출", path: "/call", iconClassName: "FaRobot" },
        { name: "픽업대 상황", path: "/pickup", iconClassName: "bi bi-speedometer2" },
        { name: "기록", path: "/history", iconClassName: "bi bi-speedometer2" },
        { name: "에러기록", path: "/error", iconClassName: "bi bi-speedometer2" },
        { name: "설정", path: "/setting", iconClassName: "bi bi-speedometer2" }

    ];
    const [isFullNav, setIsFullNav] = useState(true);

    const dbLogoSrc = sessionStorage.getItem('logoSrc');
    const logo_src = '/assets/store_logo/' + dbLogoSrc + '.png';
    // const logo_src = '/assets/store_logo/the_people_burger/the_people_burger_logo.png'

    return (
        <div id="side-nav" className={"side-nav background-color-2 " + (isFullNav ? "big-nav" : "small-nav center-align")}>
            <div id="title">
                <div onClick={() => setIsFullNav(!isFullNav)} className="toggle-menu-btn right-align">
                    {isFullNav ? (
                        <AiOutlineLeft className="icon icon-w"/>
                    ) : (
                        <AiOutlineRight className="icon icon-w"/>
                    )}
                </div>
                <div className=" center-align">
                    {isFullNav ? (
                        <img id="side-logo-img" src={"/assets/hprobot-logo/hprobot-logo-white-2line.png"} alt="hprobot-logo-2" />
                    ) : (
                        <img id="side-logo-img-short" src={"/assets/hprobot-logo/hprobot-logo-white-short.png"} alt="hprobot-logo-s" />
                    )}
                </div>
            </div>
            <NavLink to="/map" className="nav-item" activeClassName="nav-active">
                <FaMapMarkerAlt className="icon icon-w"/>
                <p>실시간 로봇위치</p>
            </NavLink>
            <NavLink to="/call" className="nav-item" activeClassName="nav-active">
                <AiOutlineRobot className="icon icon-w" />
                <p>로봇 호출</p>
            </NavLink>
            <NavLink to="/pickup" className="nav-item" activeClassName="nav-active">
                <FaParking className="icon icon-w"/>
                <p>픽업대 상황</p>
            </NavLink>
            <NavLink to="/history" className="nav-item" activeClassName="nav-active">
                <AiOutlineHistory className="icon icon-w"/>
                <p>기록</p>
            </NavLink>
            <NavLink to="/error" className="nav-item" activeClassName="nav-active">
                <MdErrorOutline className="icon icon-w"/>
                <p>에러</p>
            </NavLink>
            <NavLink to="/setting" className="nav-item" activeClassName="nav-active">
                <AiOutlineSetting className="icon icon-w"/>
                <p>설정</p>
            </NavLink>
            <div className="center-align" id="bottom">
                {isAuthorized ?
                    <NavLink to='/my-page' id="nav-user" >
                        {/* <img src={require('/assets/store_logo/the_people_burger/the_people_burger_logo.png').default} alt="logo" /> */}
                        <div className="background-color-3 user-div">
                        <img id="nav-user-logo" src={logo_src} alt="user-logo" />
                        {/* userid로 user img url 불러오기 */}
                        <p className=" text-color-w">{sessionStorage.getItem('userName')} 매장</p>
                        </div>
                    </NavLink>
                    :
                    <NavLink to='/login' id="nav-login" className="round-button background-color-3">
                        <AiOutlineUser className="icon icon-w"/>
                        <p className="text-color-w">로그인</p>
                    </NavLink>
                }
            </div>
        </div>
    );
}

export default SideNavPresenter;
