import React, {useState} from 'react'
import './../../assets/style.css';
import {postControllerLogin} from './LoginController';
import Button from '@material-ui/core/Button'; // Button을 import 한다.
// import RaisedButton from '@material-ui/core/RaisedButton'; // Button을 import 한다.
// import theme from './../../assets/theme';
// import { ThemeProvider } from '@material-ui/core/styles'
// import CssBaseline from '@material-ui/core/CssBaseline'  BiLogIn
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles';
import InputLabel from '@mui/material/InputLabel';
import OutlinedInput from '@mui/material/OutlinedInput';
import FormControl from '@mui/material/FormControl';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import InputAdornment from '@mui/material/InputAdornment';
import IconButton from '@mui/material/IconButton';
import { borders } from '@mui/system';

import LockOutlinedIcon from '@mui/icons-material/LockOutlined';    // 패스워드 아이콘
import PersonOutlineOutlinedIcon from '@mui/icons-material/PersonOutlineOutlined';  // 아이디 아이콘

// 아이콘   AiOutlineLock
import { IconContext } from "react-icons";

import {BiLogIn} from 'react-icons/bi'
import {AiOutlineLock, AiOutlineUser} from 'react-icons/ai'





const StyledTextField = withStyles({
    root: {
      '& .MuiOutlinedInput-root': {
        '& fieldset': {
          borderRadius: `90px`,
        },
      },
    },
})(TextField);
function Login() {
    // 테스트용 user 정보
    const dumpUsers = [
        { id: "hello", password: "world", mapId:7, userName:"store21"},
        { id: "admin", password: "1234", mapId:7, userName:"store14"},
        { id: "a", password: "s", mapId:7, userName:"store17"}
    ];
    const checkLogin = (id, pw) => {
        const user = dumpUsers.find(
            (user) => user.id === id && user.password === pw
        );
        if (user === undefined) {
            // alert('user undefined.'+id)
            return null;}
        return user;
    }
    // 아이디, 패스워드 입력값 변수. 초기는 null
    const [inputId, setInputId] = useState("");
    const [inputPassword, setInputPassword] = useState("");
    const [showPassword, setshowPassword] = useState("");

    // input에 입력시 변수 갱신
    const handleInputId = (e) => {
        setInputId(e.target.value)
    }

    const handleInputPassword = (e) => {
        setInputPassword(e.target.value)
    }

    const handleClickShowPassword = () => {
        setshowPassword(!showPassword);

      };
    
      const handleMouseDownPassword = (event) => {
        event.preventDefault();
      };
    const onSubmit = async () => {
        // 서버로 로그인 정보 확인
        
        let [valid, result] = await postControllerLogin(inputId, inputPassword);
        if (valid) {
            // console.log("[LoginPresenter] post login success");
            console.log("[LoginPresenter] login success : ");
            console.log(result.user_name);
            // const userName = 
            sessionStorage.setItem('userId', inputId)
            sessionStorage.setItem('mapId', result.map_id)
            sessionStorage.setItem('userName', result.user_name)
            sessionStorage.setItem('logoSrc', result.logo_src)
            document.location.href = '/'

        }
        else {
            console.log("[LoginPresenter] login failed, error  : ", result);
            alert(result);
            return;

        }
        

/*
        const user = checkLogin(inputId, inputPassword);
        if (user === null){
            console.log('입력하신 정보가 올바르지 않습니다.')
        } else {
            
            sessionStorage.setItem('userId', inputId)
            sessionStorage.setItem('mapId', user.mapId)
            sessionStorage.setItem('userName', user.userName)
            

        }
        작업 완료 되면 페이지 이동(새로고침)
        document.location.href = '/'size="3rem"
        */
    }
    return (
        <div className="content" id="login">
            <div className="content-title text-color-1"> <BiLogIn className="icon" />로그인</div>
            <div className="grid-container">
                <div className="grid-item center-align" id="item-login">
                    <img className="login-hprobot-img" src={"/assets/hprobot-logo/hprobot-logo-1line.png"} alt="hprobot-logo" />
                    {/* 한줄씩 하려고 br 태그 사용함. 나중에 부트스트랩 사용으로 간편하게 고칠예정 develop */}
                    <div className="login-input-div">
                        <div className="left-align">
                            <AiOutlineUser className="icon" />
                        </div>

                        <StyledTextField id="outlined-basic" label="아이디" variant="outlined" margin="small" style={{ width: '100% ' }}
                            size="small" value={inputId} onChange={handleInputId} color="primary" />
                        {/* InputLabelProps={{style : {color : '#688ABD'} }} 핵사코드만 적용됨 */}
                    </div>
                    <div className="login-input-div">
                        <div className="left-align">
                            <AiOutlineLock className="icon" />
                        </div>
                        <FormControl sx={{ width: '100% ' }} variant="outlined" size="small" margin="small" color="primary">
                            <InputLabel htmlFor="outlined-adornment-password" >패스워드</InputLabel>
                            {/* style={ {color : '#688ABD'}} primary로 적용됨 */}
                            <OutlinedInput
                                id="outlined-adornment-password"
                                type={showPassword ? 'text' : 'password'}
                                value={inputPassword}
                                onChange={handleInputPassword}
                                endAdornment={
                                    <InputAdornment position="end">
                                        <IconButton
                                            aria-label="toggle password visibility"
                                            onClick={handleClickShowPassword}
                                            onMouseDown={handleMouseDownPassword}
                                            edge="end"
                                        >
                                            {showPassword ? <VisibilityOff /> : <Visibility />}
                                        </IconButton>
                                    </InputAdornment>
                                }
                                label="Password"
                                sx={{ borderRadius: 25 }}
                            />
                        </FormControl>
                    </div>
                    <div>
                        <button className="round-button text-color-w background-color-1" onClick={onSubmit}>로그인</button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Login
