import React, { Component } from 'react'
import MapRail from './MapRail';
import MapStatus from './MapStatus';
import MapMoving from './MapMoving';
import RobotPosition from './RobotPosition';
import Modal from './../Modal/Modal';
import {createPortal} from 'react-dom';
// import {getDestinationRobot, getHomeRobot, getAllHomeRobot} from '../Call/CallController';
import {getMapView, getInitRobot, getInitOneRobot, getDestinationRobot, getHomeRobot, getAllHomeRobot} from './MapController';
// 아이콘   AiOutlineLock
import {GrMap} from 'react-icons/gr'

export class MapPresenter extends Component {
    constructor(props) {
        super(props)
        this.mapContainer = React.createRef()
        this.state = { unit: 0,
            isModalOpen:false,
            destinationNode:0,
            clickedRobotId:0,
            robotStatus:[],
            modalTitle:"제목",
            modalContent: "내용",
            mapView:
                [
                    { "map_id": "8", "node_id": "0", "x": "14", "y": "7", "type": "0" },
                    { "map_id": "8", "node_id": "1", "x": "11", "y": "2", "type": "6" },
                    { "map_id": "8", "node_id": "2", "x": "12", "y": "2", "type": "12" },
                    { "map_id": "8", "node_id": "3", "x": "13", "y": "2", "type": "10" },
                    { "map_id": "8", "node_id": "4", "x": "13", "y": "1", "type": "9" },
                    { "map_id": "8", "node_id": "5", "x": "12", "y": "1", "type": "12" },
                    { "map_id": "8", "node_id": "6", "x": "11", "y": "1", "type": "12" },
                    { "map_id": "8", "node_id": "7", "x": "10", "y": "1", "type": "13" },
                    { "map_id": "8", "node_id": "8", "x": "9", "y": "1", "type": "12" },
                    { "map_id": "8", "node_id": "9", "x": "8", "y": "1", "type": "12" },
                    { "map_id": "8", "node_id": "10", "x": "7", "y": "1", "type": "12" },
                    { "map_id": "8", "node_id": "11", "x": "6", "y": "1", "type": "13" },
                    { "map_id": "8", "node_id": "12", "x": "5", "y": "1", "type": "12" },
                    { "map_id": "8", "node_id": "13", "x": "4", "y": "1", "type": "12" },
                    { "map_id": "8", "node_id": "14", "x": "3", "y": "1", "type": "5" },
                    { "map_id": "8", "node_id": "15", "x": "3", "y": "2", "type": "3" },
                    { "map_id": "8", "node_id": "16", "x": "3", "y": "3", "type": "3" },
                    { "map_id": "8", "node_id": "17", "x": "3", "y": "4", "type": "2" },
                    { "map_id": "8", "node_id": "18", "x": "6", "y": "2", "type": "3" },
                    { "map_id": "8", "node_id": "19", "x": "6", "y": "3", "type": "2" },
                    { "map_id": "8", "node_id": "20", "x": "10", "y": "2", "type": "3" },
                    { "map_id": "8", "node_id": "21", "x": "10", "y": "3", "type": "2" }
                ]
        };
    }
    async componentDidMount() {
        var maxXsize = 11;
        let [error, result] = await getMapView();
        if (error) {
            console.log("[MapController] "+result);
            return;
        }
        else {
            // console.log(result);
            maxXsize=Number(result[0]['x'])+1;
            // console.log(maxXsize);
        }
        const size = this.mapContainer.current.offsetWidth;  //mapContainer 컨테이너의 가로길이 측정
        // this.setState({unit: size/maxXsize});    //MAX X 의 개수로 나눔
        console.log("[MapController] maxXsize : "+maxXsize);
        this.setState(prevState => ({
            ...prevState,
            unit: size/maxXsize,
            mapView: result
        }))
        //절대 위치 구하기
        
        // const target = document.getElementById('item-map'); // 요소의 id 값이 target이라 가정
        // const clientRect = target.getBoundingClientRect(); // DomRect 구하기 (각종 좌표값이 들어있는 객체)
        // const relativeTop = clientRect.top; // Viewport의 시작지점을 기준으로한 상대좌표 Y 값.
        // const mapContainer = document.getElementById('item-map');
        
    }
    setRobotStatus(props){
        this.setState(prevState => ({
            ...prevState,
            robotStatus: props
        }))
    }
    handelRobotClick(i){
        this.setState(prevState => ({
            ...prevState,
            isModalOpen: "robot",
            clickedRobotId: i,
            modalTitle: "로봇 복귀",
            modalContent: i+"번 로봇을 집으로 복귀시키겠습니까?"
        }))
        // nothing do
        console.log("robot modal click! "+i);
    }
    handelAllRobotClick(){
        this.setState(prevState => ({
            ...prevState,
            isModalOpen: "allRobot",
            modalTitle: "로봇 복귀",
            modalContent: "전체 로봇을 집으로 복귀시키겠습니까?"
        }))
        // nothing do
        console.log("allRobot modal click!");
    }
    handelMapClick(i){
        const occupied = this.state.robotStatus.find(robot => robot.current_node == i);
        console.log(occupied);
        if (occupied != undefined){ // 현재 어떤 로봇이 위치하고 있는 곳은 목적지 설정 불가
            return;
        }
        if (i <= this.state.robotStatus.length || i > 900){ // 1 ~ max robot id 노드는 charging station으로 치고 목적지 불가
            // 900 초과 node는 901~910이며, 로봇의 초기화시 위치할 가상의 공간이므로 목적지 설정 불가
            return;
        }
        
        this.setState(prevState => ({
            ...prevState,
            isModalOpen: "map",
            destinationNode: i,
            modalTitle: "로봇 보내기",
            modalContent: i+"번 테이블로 로봇을 보내겠습니까?"
        }))
        // nothing do
        console.log("map modal click! "+i);
    }
    handleInitClick = () => {
        this.setState(prevState => ({
            ...prevState,
            isModalOpen: "init",
            modalTitle: "로봇 초기화",
            modalContent: "전체 로봇을 초기화하시겠습니까?"
        }))
        // nothing do
        console.log("init modal click!");
    }
    handleInitOneClick = (i) => {
        this.setState(prevState => ({
            ...prevState,
            isModalOpen: "initOne",
            clickedRobotId: i,
            modalTitle: "로봇 초기화",
            modalContent: i+"번 로봇을 초기화하시겠습니까?"
        }))
        // nothing do
        // console.log("init one modal click!");
    }
    async clickInitModalPermit(userId){

        let [error, message] = await getInitRobot(userId);
        if (!error) {
            console.log("[MapPresenter] getInitRobot success : ", message);
            this.setState(prevState => ({
                ...prevState,
                isModalOpen: false
            }))
        }
        else {
            console.log("[MapPresenter] getInitRobot fail  : ", message);
            this.setState(prevState => ({
                ...prevState,
                isModalOpen: false
            }))
        }
    }
    async clickInitOneModalPermit(userId){
        const clickedRobotId = this.state.clickedRobotId;

        let [error, message] = await getInitOneRobot(userId, clickedRobotId);
        if (!error) {
            console.log("[MapPresenter] getInitOneRobot success : ", message);
            this.setState(prevState => ({
                ...prevState,
                isModalOpen: false,
                clickedRobotId: 0
            }))
        }
        else {
            console.log("[MapPresenter] getInitOneRobot fail  : ", message);
            this.setState(prevState => ({
                ...prevState,
                isModalOpen: false,
                clickedRobotId: 0
            }))
        }
    }
    async clickRobotModalPermit(userId){
        const clickedRobotId = this.state.clickedRobotId;
        let [error, message] = await getHomeRobot(userId, clickedRobotId);
        if (!error) {
            console.log("[MapPresenter] getHomeRobot success : ", message);
            this.setState(prevState => ({
                ...prevState,
                isModalOpen: false,
                clickedRobotId: 0
            }))
        }
        else {
            console.log("[MapPresenter] getHomeRobot fail  : ", message);
            this.setState(prevState => ({
                ...prevState,
                isModalOpen: false
            }))
        }
    }
    async clickAllRobotModalPermit(userId){
        let [error, message] = await getAllHomeRobot(userId);
        if (!error) {
            console.log("[MapPresenter] getAllHomeRobot success : ", message);
            this.setState(prevState => ({
                ...prevState,
                isModalOpen: false
            }))
        }
        else {
            console.log("[MapPresenter] getAllHomeRobot fail  : ", message);
            this.setState(prevState => ({
                ...prevState,
                isModalOpen: false
            }))
        }
    }
    async clickMapModalPermit(userId){
        const destinationNode = this.state.destinationNode;

        let [error, message] = await getDestinationRobot(userId, destinationNode);
        if (!error) {
            console.log("[MapPresenter] getDestinationRobot success : ", message);
            this.setState(prevState => ({
                ...prevState,
                isModalOpen: false,
                destinationNode: 0
            }))
        }
        else {
            console.log("[MapPresenter] getDestinationRobot fail  : ", message);
            this.setState(prevState => ({
                ...prevState,
                isModalOpen: false
            }))
        }
    }

    render() {
        
        
        const userId = sessionStorage.getItem('userId');
        const isModalOpen = this.state.isModalOpen;
        const robotStatus = this.state.robotStatus;
        const unit= this.state.unit;
        const colorDescription = [["이동", "#00E03F"],["정지", "#E00000"],["충전", "#FF8C21"],["서버 X", "gray"],["그외", "black"]]
        return (
            <div className="content" id="map">
                <div className="content-title text-color-1"> <GrMap className="icon" />실시간 로봇위치</div>

                <div className="grid-container">

                    <div className="grid-item item-scroll" id="item-map">
                        <div id="map-top">
                            <p id="map-name" className="round-button background-color-3 text-color-w">{sessionStorage.getItem('userName')} 매장</p>

                            <div id="color-description">
                                    {colorDescription.map(Item => (
                                        // <PickupContent pickup={pickupItem} key={pickupList.id}/>
                                        <>
                                            <p id="description-circle" className="circle" style={{ borderColor: Item[1] }}></p>
                                            <p>{Item[0]}</p>
                                        </>
                                    ))}
                            </div>

                        </div>
                        <div ref={this.mapContainer} id="map-container">
                            <RobotPosition unit={unit} setRobotStatus={p => this.setRobotStatus(p)}/>
                            <MapRail xytypeList={this.state.mapView} unit={unit} onClick={i=>this.handelMapClick(i)}/>

                        </div>

                    </div>
                    <div className="grid-item" id="item-status">
                        <div className="grid-title background-color-3 text-color-w">로봇 복귀버튼</div>
                        <div className="item-scroll">
                            <ul>
                                {robotStatus.map(robot => (
                                    <MapStatus robot={robot} key={robot.robot_id}  onClick={() => this.handelRobotClick(robot.robot_id)}/>
                                ))}
                                <li className="home-button round-button background-color-4 " onClick={() => this.handelAllRobotClick()}> 
                                    <span className="text-color-w"> 전체 복귀</span>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div className="grid-item" id="item-arrive">
                        <div className="grid-title background-color-3 text-color-w">도착 지점 목록</div>
                            <ul>
                                {robotStatus.map(robot => (
                                    <MapMoving robot={robot} key={robot.robot_id} />
                                ))}
                            </ul>
                    </div>
                    <div className="grid-item" id="item-init">
                        <div className="grid-title background-color-3 text-color-w">로봇 초기화</div>
                        <div>
                            <ol>
                                <span className="round-button background-color-5 text-color-w" style={{marginLeft: '0.3rem', padding: "0.3rem 0.6rem"}}
                                onClick={this.handleInitClick}>전체</span>  
                                {robotStatus.map(robot => (
                                    <span className="round-button background-color-5 text-color-w" style={{marginLeft: '0.3rem', padding: "0.3rem 0.6rem"}} key={robot.robot_id}
                                    onClick={() => this.handleInitOneClick(robot.robot_id)}>{robot.robot_id}</span>
                                ))}
                            </ol>
                        </div>
                    </div>
                </div>
                <Modal title={this.state.modalTitle} content={this.state.modalContent} open={isModalOpen} onCancel={() => this.setState(prevState => ({ isModalOpen: false }))}
                    onPermit={isModalOpen == "map" ? () => this.clickMapModalPermit(userId)
                        : isModalOpen == "robot" ? () => this.clickRobotModalPermit(userId)
                        : isModalOpen == "allRobot" ? () => this.clickAllRobotModalPermit(userId)
                            : isModalOpen == "init" ? () => this.clickInitModalPermit(userId)
                                : () => this.clickInitOneModalPermit(userId)}>
                </Modal>
            </div>
        )
    }
}

export default MapPresenter
