import React from 'react';
import {createPortal} from 'react-dom';

function Modal({ title, content, open, onPermit, onCancel }) {
  if (!open) return null;
  return createPortal(
    <div id="modal">
      <div id="overlay" />
      <div id="modal-box" className="background-color-w">
        <h3 id="modal-title" className="background-color-5 text-color-w">{title}</h3>
        <div id="modal-content">{content}</div>
        <div id="button-div">
          <button id="ok-button" className="round-button background-color-5 text-color-w" onClick={onPermit}>확인</button>
          <button id="cancle-button" className="round-button background-color-5 text-color-w" onClick={onCancel}>취소</button>
        </div>
      </div>
    </div>,
    document.getElementById('modal')

  )
}

export default Modal
